* {
  padding: 0;
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
}

#exchangeIcon {
  display: block;
  margin: auto;
  justify-content: center;
  opacity: 0.92;
  max-height: 90px;
  max-width: 90px;
  transition: 0.1s ease-in-out;
  z-index: 1;
  cursor: pointer;
}

#exchangeIcon:hover {
  color: black;
  opacity: 1;
}

@media (min-width: 991.98px) {
  #exchangeIcon {
    margin-top: 50px;
  }

  #title {
    margin-top: 50px;
  }
  #container {
    margin-top: 20px;
  }
}

#card {
  background-color: aliceblue !important;
  border: 1px solid #348ac4;
  border-radius: 10px;
  transition: 0.1s ease-in;
}

#currencyElement {
  padding: 5px;
}

#card:hover {
  border-bottom: 5px solid #348ac4;
}

#inputBox[type="number"] {
  width: 90%;
  margin: auto;
  display: block;
  padding: 6px 10px;
  font-size: 24px;
  font-family: "Inconsolata", monospace;
  text-align: center;
  background-color: white;
  color: black;
  border: 3px solid #348ac4;
  border-radius: 10px;
  outline: none;
  transition: 0.3s all ease;
}

#inputBox[type="number"]:hover {
  border: 3px solid #004896;
  padding: 6px 10px;
  width: 95%;
}

#selectItem {
  font-size: 20px;
  text-align-last: center;
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
}

#selectItem,
#currencyElement {
  margin-top: 3px;
  margin-bottom: 3px;
}

@media (min-width: 991.98px) {
  #currencyElement {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 20px 0;
  }

  #selectItem {
    margin-top: 10px;
  }
}

h2 {
  text-align: center;
  padding: 40px 0;
}

@media (max-width: 991.98px) {
  h2 {
    padding: 20px 0;
    font-size: 20px;
  }
}

